const options = {
  baseUrl: "https://mesto.nomoreparties.co/v1",
  groupID: "cohort-26",
  headers: {
    authorization: "05288f01-26d1-4add-96c0-b100674c662e",
    "Content-Type": "application/json"
  }
}

export function getInitialCards() {
  return fetch(`${this._baseUrl}/${this._groupID}/cards`, {
    headers: {
      authorization: this._headers.authorization
    }
  })
    .then(handleResult);
}

export function getProfileData() {
  return fetch(`${this._baseUrl}/${this._groupID}/users/me`, {
    headers: {
      authorization: this._headers.authorization
    }
  })
    .then(handleResult);
}

export function setAvatar(avatar) {
  return fetch(`${this._baseUrl}/${this._groupID}/users/me/avatar`, {
    method: "PATCH",
    headers: this._headers,
    body: JSON.stringify({
      avatar: avatar
    })
  })
    .then(handleResult);
}

export function setProfileData(name, about) {
  return fetch(`${this._baseUrl}/${this._groupID}/users/me`, {
    method: "PATCH",
    headers: this._headers,
    body: JSON.stringify({
      name,
      about
    })
  })
    .then(handleResult);
}

export function createCard(name, link) {
  return fetch(`${this._baseUrl}/${this._groupID}/cards`, {
    method: "POST",
    headers: this._headers,
    body: JSON.stringify({
      name,
      link
    })
  })
    .then(handleResult);
}

export function removeCard(id) {
  return fetch(`${this._baseUrl}/${this._groupID}/cards/${id}`, {
    method: "DELETE",
    headers: this._headers
  })
    .then(handleResult);
}

export function likeCard(id) {
  return fetch(`${this._baseUrl}/${this._groupID}/cards/likes/${id}`, {
    method: "PUT",
    headers: this._headers
  })
    .then(handleResult);
}

export function unlikeCard(id) {
  return fetch(`${this._baseUrl}/${this._groupID}/cards/likes/${id}`, {
    method: "DELETE",
    headers: this._headers
  })
    .then(handleResult);
}

function handleResult(res) {
  if (res.ok) { return res.json() }
  return Promise.reject(`Статут ошибки: ${res.status}`);
}
